
.App-header {
    text-align: center;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    font-weight: bold;
}


.calculator-wrapper {
    border: 1px solid #61dafb;
    padding: 10px;
    border-radius: 5px;
    width: 150px;
}

.count {
    background-color: #61dafb;
    color: #282c34;
    border-radius: 5px;
    margin-bottom: 5px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.max {
    color: red;
}

.button-wrapper {
    border: 1px solid #61dafb;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-around;
}

.button {
    border-radius: 5px;
    background-color: #61dafb;
    color: #282c34;
    width: 60px;
    border: none;
}

.button:disabled {
    opacity: 0.5;
}

